import classes from "./TextBoxUI.module.css";
import VisibilityIcon from "@mui/icons-material/Visibility";
import { COLOR_SCHEME } from "../Color/ColorScheme";

export function NormalInbox(props) {
  return (
    <div>
      <input
        type="text"
        onChange={props.onChng}
        placeholder={props.placehldr}
        className={classes.normalInbox}
        required={props.rqrd ? true : false}
      />
    </div>
  );
}

export function PasswordInbox(props) {
  return (
    <div>
      <div className={classes.relativePositionStyle}>
        <input
          type={props.showPwdButton ? "text" : "password"}
          onChange={props.onChng}
          placeholder={props.placehldr}
          className={classes.passwordInbox}
          required={props.rqrd ? true : false}
        />
        <VisibilityIcon
          onClick={props.pwdVisibilityHandler}
          style={props.showPwdButton ? { color: COLOR_SCHEME.color } : { color: "#aaa" }}
          className={classes.visibilityIconStyle}
        />
      </div>
    </div>
  );
}
