import classes from "./ButtonUI.module.css";

export function ContainedButton(props) {
  return (
    <div className={classes.btnContainedDiv}>
      <button type={props.type} className={classes.containedBtnStyle}>
        {props.btnName}
      </button>
    </div>
  );
}

export function TransparentButton(props) {
  return (
    <div className={classes.btnTransparentDiv}>
      <button type={props.type} className={classes.transparentBtnStyle}>
        {props.btnName}
      </button>
    </div>
  );
}
