import { Typography } from "@mui/material";
import classes from "./TypographyUI.module.css";

export function Heading(props) {
  return (
    <div>
      <Typography variant="h1" className={classes.headingStyle}>
        {props.headingText}
      </Typography>
    </div>
  );
}

export function SubHeading(props) {
  return (
    <div>
      <Typography variant="h2">{props.subHeadingText}</Typography>
    </div>
  );
}

export function Paragraph(props) {
  return (
    <div>
      <Typography variant="p">{props.paragraphText}</Typography>
    </div>
  );
}

export function Label(props) {
  return (
    <div>
      <Typography variant="subtitle1" className={classes.labelStyle}>
        {props.labelText}
      </Typography>
    </div>
  );
}

export function Caption(props) {
  return (
    <div>
      <Typography variant="subtitle2" className={classes.captionStyle}>
        {props.captionText}
      </Typography>
    </div>
  );
}

export function CombinationTypography(props) {
  return (
    <div className={classes.combinationTypographyContainerStyle}>
      <Typography variant="subtitle2" className={classes.captionStyle}>
        {props.captionText1} <span className={classes.boldCaptionStyle}>{props.captionText2}</span>
      </Typography>
    </div>
  );
}
