import { Box, Container, Grid } from "@mui/material";
import { useState } from "react";
import loginImage from "../../../images/1.jpg";
import classes from "./PublisherLogin.module.css";
import { Heading, Caption, Label, CombinationTypography } from "../../../UI/Typography/TypographyUI";
import { NormalInbox, PasswordInbox } from "../../../UI/TextBox/TextBoxUI";
import { ContainedButton, TransparentButton } from "../../../UI/Button/ButtonUI";

export default function AuthorLogin() {
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);

  function formSubmithandler(e) {
    e.preventDefault();
    alert(`Successfully logged in. Username: ${userName}. Password: ${password}.`);
  }

  function usrNameChngHandler(e) {
    setUserName(e.target.value);
  }

  function pwdChngHandler(e) {
    setPassword(e.target.value);
  }

  return (
    <div>
      <Grid container>
        <Grid item lg={6} md={12} sm={12} xs={12}>
          <Box display="flex" justifyContent="center" alignItems="center" style={{ height: "100%" }}>
            <Container maxWidth="xs">
              <div style={{ width: "100%", marginTop: "50px" }}>
                <form onSubmit={formSubmithandler}>
                  <Heading headingText="Publisher Login" />
                  <Caption captionText="Welcome back! Please enter your details." />
                  <Label labelText="Username" />
                  <NormalInbox rqrd={true} placehldr="Enter your username" onChng={usrNameChngHandler} />
                  <Label labelText="Password" />
                  <PasswordInbox
                    rqrd={true}
                    placehldr="Enter your password"
                    showPwdButton={showPassword}
                    pwdVisibilityHandler={() => {
                      setShowPassword(!showPassword);
                    }}
                    onChng={pwdChngHandler}
                  />
                  <TransparentButton type="button" btnName="Forgot password?" />
                  <ContainedButton type="submit" btnName="Login" />
                  <CombinationTypography captionText1="Don't have an account? " captionText2="Contact Admin" />
                </form>
              </div>
            </Container>
          </Box>
        </Grid>
        <Grid item lg={6} md={12} sm={12} xs={12}>
          <img src={loginImage} alt="login page" className={classes.imageSize} />
        </Grid>
      </Grid>
    </div>
  );
}
